/*
Sidebar css
*/
.sidebar {
  float: left;
  position: relative;
  width: 20%;
  height: 100vh;
  background: #2c2c35;
  color: #fff;
  border-right: 2px solid #393942;

  .header {
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    border-bottom: 2px solid #393942;
    .header-text {
      text-align: center;
      // font-size: 2em;
    }
  }

  .footer {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    left: 0;
    bottom: 0;
    right: 0;
    height: 50px;
    border-top: 2px solid #393942;

    .footer-item {
      display: inline-grid;
      padding: 15px;
    }
  }
}
