/*
Chat css
*/
.chat {
  float: right;
  position: relative;
  width: 80%;
  height: 100vh;
  background: #20232B;
  color: #fff;

  .no-chat-selected {
    position: absolute;
    top: 50%;
    left: 40%;
    padding: 10px;
    text-align: center;
    vertical-align: middle;
    background: #2c2c35;
    border-radius: 50px;
    width: 300px;
    font-size: 0.9em;
  }
}
